import { FunctionComponent } from 'preact';
import { classes } from '@adobe-commerce/elsie/lib';
import { PurchaseOrderConfirmationContentProps } from '@/b2b-purchase-order/types/components';
import { useText } from '@adobe-commerce/elsie/i18n';
import '@/b2b-purchase-order/components/PurchaseOrderConfirmationContent/PurchaseOrderConfirmationContent.css';

export const PurchaseOrderConfirmationContent: FunctionComponent<
  PurchaseOrderConfirmationContentProps
> = ({
  className,
  purchaseOrderNumber,
  routePurchaseOrderDetails,
  ...props
}) => {
  const t = useText({
    title: 'PurchaseOrders.purchaseOrderConfirmation.title',
    messagePrefix: 'PurchaseOrders.purchaseOrderConfirmation.messagePrefix',
    messageSuffix: 'PurchaseOrders.purchaseOrderConfirmation.messageSuffix',
  });

  return (
    <div
      {...props}
      className={classes(['purchase-orders-confirmation-content', className])}
    >
      <h2 className="purchase-orders-confirmation-content__title">{t.title}</h2>
      <p className="purchase-orders-confirmation-content__message">
        {t.messagePrefix}{' '}
        <a
          className="purchase-orders-confirmation-content__link"
          href={routePurchaseOrderDetails() ?? ''}
        >
          #{purchaseOrderNumber}
        </a>
        . {t.messageSuffix}
      </p>
    </div>
  );
};
