import { classes, Container } from '@adobe-commerce/elsie/lib';
import { PurchaseOrderConfirmationContent } from '@/b2b-purchase-order/components';
import { PurchaseOrderConfirmationProps } from '@/b2b-purchase-order/types/containers';

export const PurchaseOrderConfirmation: Container<
  PurchaseOrderConfirmationProps
> = ({
  className,
  purchaseOrderNumber,
  routePurchaseOrderDetails,
  ...props
}) => {
  if (!purchaseOrderNumber) return null;

  return (
    <div
      {...props}
      className={classes(['b2b-purchase-order-confirmation', className])}
    >
      <PurchaseOrderConfirmationContent
        purchaseOrderNumber={purchaseOrderNumber}
        routePurchaseOrderDetails={routePurchaseOrderDetails}
      />
    </div>
  );
};
